.mylistings-container {
  padding: 2rem;
  background: #f9fafb;
  min-height: 100vh;
}

.stats-header {
  display: flex;
  gap: 1rem;
  align-items: center;
  margin-bottom: 2rem;
  flex-wrap: wrap;
}

.stat-box {
  background: white;
  padding: 1rem 2rem;
  border-radius: 12px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.08);
  flex: 1;
  text-align: center;
}

.stat-box p {
  font-size: 0.8rem;
  color: #666;
  margin-bottom: 0.25rem;
}

.stat-box h2 {
  font-size: 1.5rem;
  margin: 0;
  color: #111;
}

.add-btn {
  background: #2563eb;
  color: white;
  padding: 0.8rem 1.5rem;
  border-radius: 10px;
  text-decoration: none;
  font-weight: bold;
  transition: background 0.2s ease;
}

.add-btn:hover {
  background: #1d4ed8;
}

.listings-section {
  margin-top: 2rem;
}

.listings-section h2 {
  margin-bottom: 1rem;
  font-size: 1.3rem;
  font-weight: 600;
}

.listing-card {
  background: white;
  border-radius: 12px;
  padding: 1rem;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 6px rgba(0,0,0,0.08);
}

.listing-left {
  display: flex;
  gap: 1rem;
  align-items: flex-start;
}

.listing-icon {
  font-size: 2rem;
}

.listing-info h3 {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 600;
}

.listing-info p {
  margin: 0.25rem 0;
  color: #555;
  font-size: 0.9rem;
}

.listing-tags {
  margin-top: 0.3rem;
}

.tag {
  background: #e0f2fe;
  color: #0369a1;
  padding: 0.2rem 0.6rem;
  border-radius: 8px;
  font-size: 0.75rem;
  margin-right: 0.5rem;
}

.tag.red {
  background: #fee2e2;
  color: #b91c1c;
}

.qty {
  font-size: 0.8rem;
  color: #444;
}

.listing-meta {
  margin-top: 0.3rem;
  font-size: 0.75rem;
  color: #777;
  display: flex;
  gap: 1rem;
  align-items: center;
}

.listing-actions {
  text-align: right;
}

.status {
  display: block;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
  font-weight: 500;
}

.status.available {
  color: #059669;
}

.action-buttons button {
  border: none;
  background: none;
  font-size: 0.85rem;
  cursor: pointer;
  margin-left: 0.5rem;
  display: inline-flex;
  align-items: center;
  gap: 0.3rem;
  padding: 0.4rem 0.6rem;
  border-radius: 6px;
  transition: background 0.2s ease;
}

.action-buttons .edit {
  color: #2563eb;
}

.action-buttons .edit:hover {
  background: #e0e7ff;
}

.action-buttons .delete {
  color: #dc2626;
}

.action-buttons .delete:hover {
  background: #fee2e2;
}

.page-title {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0.2rem;
  color: #111;
}

.page-subtitle {
  font-size: 0.95rem;
  color: #555;
  margin-bottom: 1.5rem;
}

.listing-left {
  display: flex;
  gap: 1rem;
  align-items: flex-start;
}

.listing-image {
  width: 120px;
  height: 120px;
  flex-shrink: 0;
  background-color: #f3f4f6;
  border-radius: 8px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.listing-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.listing-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}


.edit-form {
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 100%;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 4px;
}

.edit-form input,
.edit-form select {
  padding: 6px 8px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.edit-actions {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.edit-actions button {
  padding: 6px 12px;
  border-radius: 6px;
  cursor: pointer;
}

.edit-actions button:first-child {
  background: #007bff;
  color: white;
  border: none;
}

.edit-actions button:last-child {
  background: #eee;
  border: none;
}
